import { ElButton, ElMessage, ElInput } from "element-plus";
export default {
  components: {
    ElInput
  },
  data() {
    return {
      // 号码信息
      car_number: "",
      phone_number: ""
    };
  },
  methods: {
    async analysicNumber() {
      if (!this.car_number && !this.phone_number) {
        ElMessage.warning('请选择号码');
        return;
      }
      try {
        let prompt = "我需要根据数字能量学测算我的号码运势,这是信息：";
        let msg = "";
        if (this.car_number != '') {
          msg += "车牌号:" + this.car_number;
        }
        if (this.phone_number != '') {
          msg += "手机号:" + this.phone_number;
        }
        msg = prompt + msg;
        const encodedDescription = encodeURIComponent(msg);
        try {
          const newWindow = window.open(`/#/chat?user_input=${encodedDescription}`);
          if (!newWindow || newWindow.closed || typeof newWindow.closed === 'undefined') {
            window.location.href = `/#/chat?user_input=${encodedDescription}`;
          }
        } catch (error) {
          window.location.href = `/#/chat?user_input=${encodedDescription}`;
        }
      } catch (error) {
        ElMessage.error('生成号码信息时出错');
        console.error(error);
      } finally {}
    }
  }
};